"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import toast from "react-hot-toast";

import { apiFetch } from "@/lib/api-client";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";

type Course = {
  id: number;
  code: string;
  title: string;
  description: string | null;
};

type Enrollment = {
  id: number;
  status: "pending" | "approved" | "rejected" | "dropped";
  remarks: string | null;
  requested_at: string | null;
  decided_at: string | null;
  course_id: number;
  course_code: string;
  course_title: string;
};

export default function StudentEnrollmentPage() {
  const [courses, setCourses] = useState<Course[]>([]);
  const [enrollments, setEnrollments] = useState<Enrollment[]>([]);
  const [loading, setLoading] = useState(true);

  const loadData = async () => {
    try {
      setLoading(true);
      const [courseRes, enrollmentRes] = await Promise.all([
        apiFetch("/student/courses"),
        apiFetch("/student/enrollments"),
      ]);

      setCourses((courseRes as { courses: Course[] }).courses ?? []);
      setEnrollments((enrollmentRes as { enrollments: Enrollment[] }).enrollments ?? []);
    } catch (error) {
      toast.error(error instanceof Error ? error.message : "Failed to load enrollment data.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadData();
  }, []);

  const handleEnroll = async (courseId: number) => {
    try {
      await apiFetch("/student/enrollments", {
        method: "POST",
        body: JSON.stringify({ course_id: courseId }),
      });

      toast.success("Enrollment request submitted.");
      loadData();
    } catch (error) {
      toast.error(error instanceof Error ? error.message : "Enrollment failed.");
    }
  };

  const pendingOrApprovedCourseIds = new Set(
    enrollments
      .filter((e) => e.status === "pending" || e.status === "approved")
      .map((e) => e.course_id)
  );

  return (
    <main className="min-h-screen bg-slate-50 p-6">
      <div className="max-w-6xl mx-auto space-y-6">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-slate-900">Enrollment</h1>
            <p className="text-slate-600">Submit and track your enrollment requests.</p>
          </div>
          <Link href="/student">
            <Button variant="outline">Back to Dashboard</Button>
          </Link>
        </div>

        <Card>
          <CardHeader>
            <CardTitle>Available Courses</CardTitle>
            <CardDescription>Choose a course and send an enrollment request.</CardDescription>
          </CardHeader>
          <CardContent>
            {loading ? (
              <p className="text-slate-500">Loading courses...</p>
            ) : (
              <div className="space-y-3">
                {courses.map((course) => {
                  const locked = pendingOrApprovedCourseIds.has(course.id);

                  return (
                    <div key={course.id} className="border rounded-lg p-4 flex items-center justify-between">
                      <div>
                        <p className="font-semibold text-slate-900">{course.code} - {course.title}</p>
                        <p className="text-sm text-slate-600">{course.description ?? "No description"}</p>
                      </div>
                      <Button disabled={locked} onClick={() => handleEnroll(course.id)}>
                        {locked ? "Already Requested" : "Request Enrollment"}
                      </Button>
                    </div>
                  );
                })}
              </div>
            )}
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>My Enrollment Requests</CardTitle>
            <CardDescription>Status of your submitted requests.</CardDescription>
          </CardHeader>
          <CardContent>
            {loading ? (
              <p className="text-slate-500">Loading requests...</p>
            ) : enrollments.length === 0 ? (
              <p className="text-slate-500">No requests yet.</p>
            ) : (
              <div className="space-y-3">
                {enrollments.map((row) => (
                  <div key={row.id} className="border rounded-lg p-4 flex items-center justify-between">
                    <div>
                      <p className="font-medium text-slate-900">{row.course_code} - {row.course_title}</p>
                      <p className="text-xs text-slate-500">Requested: {row.requested_at ? new Date(row.requested_at).toLocaleString() : "-"}</p>
                      {row.remarks && <p className="text-xs text-amber-700 mt-1">Remarks: {row.remarks}</p>}
                    </div>
                    <Badge>{row.status}</Badge>
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </main>
  );
}
